.toolbar{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: #ff5200;
    height: 56px;
}
.toolbar_navigation{
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 1rem;
}
.toolbar_navigation {
    color: white;
    text-decoration: none;
}
.toolbar_navigation a:hover,
.toolbar_navigation a:active{
    color: #fa923f;
}
.toolbar_nav-items ul{
    list-style: none;
    margin: 0;
    padding: 0;
    display:flex;
}
.toolbar_nav-items li{
    padding:0 0.5rem;
}
.toolbar_logo{
    margin-left: 1rem;
}

.toolbar_logo {
    
    text-decoration: none;
    font: 1.5rem;
}
.spacer{
    flex: 1;
}

@media (max-width:768px){
    .toolbar_nav-items{
    display:none;
    }
}

.dropdown {
    position: relative;
    display: inline-block;
    color:white;
    margin-left:50px;
    font-size:18px;
  }
  
  .dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    color:black;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    padding: 12px 16px;
    z-index: 1;
  }
  
  .dropdown:hover .dropdown-content {
    display: block;
  }

.links{
    color:black !important;
}